import React from 'react'
import Matter from "matter-js";
import {Modal, Radio} from "antd";
import BotUI from '@chentsulin/react-botui';

let assets = []
let num = 10
for (let i = 1; i <= num; i++) {
    assets.push("https://a-vat.oss-cn-beijing.aliyuncs.com/bottle" + i + ".svg")
}
let greetings = ["一壶浊酒喜相逢，古今多少事，都付笑谈中。", "白发渔樵江渚上，惯看秋月春风。", "是非成败转头空，青山依旧在，几度夕阳红。", "滚滚长江东逝水，浪花淘尽英雄。", "几度夕阳红。白发渔樵江渚上，都付笑谈中。", "一壶浊酒喜相逢，古今多少事，滚滚长江东逝水，浪花淘尽英雄。", "是非成败转头空，青山依旧在，惯看秋月春风。", "收到", "好的", "你好呀啦啦啦", "我懂啦", "我没懂呢，你在说啥呀", "啊啊啊？", "“Avat”源自“Brain in a vat”，意为缸中之脑的载体。", "“Avat”源自“Brain in a vat”，意为缸中之脑的载体。", "“Avat”源自“Brain in a vat”，意为缸中之脑的载体。", "什么事呀", "好的吧？", "在漂流世界中，与你交流的是真实的人还是虚拟的人呢？", "在漂流世界中，与你交流的是真实的人还是虚拟的人呢？", "在漂流世界中，与你交流的是真实的人还是虚拟的人呢？", "那你呢？", "啥情况呀", "随便吧", "你该如何保证自己不处于“Brain in a vat”呢？", "《Avat》是一个针对心理学和虚拟交流的科普和实验平台", "《Avat》是一个针对心理学和虚拟交流的科普和实验平台", "《Avat》是一个针对心理学和虚拟交流的科普和实验平台", "啊这…", "啊这…", "啊这…"]

const request = require('request');

const Engine = Matter.Engine,
    Render = Matter.Render,
    Runner = Matter.Runner,
    Composites = Matter.Composites,
    Common = Matter.Common,
    MouseConstraint = Matter.MouseConstraint,
    Mouse = Matter.Mouse,
    Composite = Matter.Composite,
    Bodies = Matter.Bodies,
    Events = Matter.Events;

const select = function (x, y) {
    let scale = window.innerWidth / 1280
    if (Common.random(0, 1) < 0.9) {
        let seed = Common.random(0, 1)
        if (seed < 0.7) {
            return Bodies.rectangle(x, y, Common.random(60 * scale, 100 * scale), Common.random(30 * scale, 50 * scale), {
                render: {
                    fillStyle: 'white',
                    strokeStyle: 'black',
                    lineWidth: 1
                }
            });
        } else {
            return Bodies.polygon(x, y, 3, 15 * scale, {
                render: {
                    fillStyle: 'white',
                    strokeStyle: 'black',
                    lineWidth: 1
                }
            });
        }
    } else {
        return Bodies.rectangle(x, y, 150 * scale, 200 * scale, {
            render: {
                sprite: {
                    texture: assets[Math.round(Common.random(0, num - 1))],
                    xScale: 1.7 * scale,
                    yScale: 1.7 * scale
                }
            }
        });
    }
}

export default class Home extends React.Component {
    oldStack = [];
    state = {
        world: null,
        engine: null,
        updateInterval: null,
        addInterval: null,
        removeInterval: null,
        chatInterval: null,
        mod: 0,
        lastClickTime: 0,
        lastChatTime: 0,
        showModal: false,
        messages: [
            {
                content: '想聊点什么呢？',
            }
        ],
        ground: Bodies.rectangle((window.innerWidth + 500) / 2, window.innerHeight + 10, window.innerWidth + 500, 10, {isStatic: true}),
    }

    componentWillMount() {
        const that = this;
        const engine = Engine.create();
        const world = engine.world;
        const render = Render.create({
            element: document.body,
            engine: engine,
            options: {
                width: window.innerWidth,
                height: window.innerHeight,
                pixelRatio: 1, // 设置像素比
                background: '#e9e9e9', // 全局渲染模式时背景色
                hasBounds: false,
                enabled: true,
                wireframes: false, // 线框模式
                showSleeping: true, // 刚体睡眠状态
                showDebug: false, // Debug 信息
                showBroadphase: false, // 粗测阶段
                showBounds: false, // 刚体的界限
                showVelocity: false, // 移动刚体时速度
                showCollisions: false, // 刚体碰撞点
                showSeparations: false, // 刚体分离
                showAxes: false, // 刚体轴线
                showPositions: false, // 刚体位置
                showAngleIndicator: false, // 刚体转角指示
                showIds: false, // 显示每个刚体的 ID
                showVertexNumbers: false, // 刚体顶点数
                showConvexHulls: false, // 刚体凸包点
                showInternalEdges: false, // 刚体内部边界
                showMousePosition: false // 鼠标约束线
            }
        });

        Render.run(render);

        // create runner
        const runner = Runner.create();
        Runner.run(runner, engine);

        // add bodies
        Composite.add(world, [
            // walls,
            that.state.ground,
            Bodies.rectangle(window.innerWidth + 10, (window.innerHeight + 10) / 2, 10, window.innerHeight + 500, {isStatic: true}),
            Bodies.rectangle(-10, (window.innerHeight + 10) / 2, 10, window.innerHeight + 500, {isStatic: true}),
        ]);

        const chatInterval = setInterval(function () {
            const time = new Date().getTime();
            if (that.state.showModal && time - that.state.lastChatTime > 15000) {
                that.setState({
                    showModal: false,
                })
                that.oldStack.forEach((stack) => {
                    stack.bodies.forEach((body) => {
                        body.isStatic = false
                    })
                })
                if (that.state.mod === 0) {
                    that.UpdateMod(1)
                }
            }
        }, 1000);

        this.setState({
            world,
            chatInterval
        })

        const stack = Composites.stack(20, 20, 6, 5, 0, 0, select);

        Composite.add(world, stack);
        this.oldStack.push(stack)


        // add mouse control
        const mouse = Mouse.create(render.canvas),
            mouseConstraint = MouseConstraint.create(engine, {
                mouse: mouse,
                constraint: {
                    stiffness: 0.2,
                    render: {
                        visible: false
                    }
                }
            });
        Events.on(mouseConstraint, 'enddrag', function () {
            const time = new Date().getTime();
            if (time - that.state.lastClickTime < 300) {
                that.setState({
                    showModal: true,
                    messages: [
                        {
                            content: '想聊点什么呢？',
                        }
                    ],
                    lastChatTime: time
                })
                that.oldStack.forEach((stack) => {
                    stack.bodies.forEach((body) => {
                        body.isStatic = true
                    })
                })
            }
            that.setState({
                lastClickTime: time
            })
        })

        Composite.add(world, mouseConstraint);

        // keep the mouse in sync with rendering
        render.mouse = mouse;

        // fit the render viewport to the scene
        Render.lookAt(render, {
            min: {x: 0, y: 0},
            max: {x: window.innerWidth, y: window.innerHeight}
        });

    }

    handleAction = async res => {
        let that = this
        that.setState({
            messages: [
                ...that.state.messages,
                {
                    human: true,
                    content: res.value,
                },
            ],
            lastChatTime: new Date().getTime()
        });
        request({
            url: 'https://api.ownthink.com/bot?appid=6a31c2b735f618d763001f3cb695e148&userid=SBXpcggY&spoken=' + res.value + "?",
            timeout: 1500
        }, function (error, response, body) {
            if (!error && response.statusCode === 200) {
                that.setState({
                    messages: [
                        ...that.state.messages,
                        {
                            content: JSON.parse(body).data.info.text
                        },
                    ],
                    lastChatTime: new Date().getTime()
                });
            } else {
                that.setState({
                    messages: [
                        ...that.state.messages,
                        {
                            content: greetings[[Math.round(Common.random(0, greetings.length - 1))]]
                        },
                    ],
                    lastChatTime: new Date().getTime()
                });
            }
        })
    };

    UpdateMod = function (value) {
        let that = this
        that.setState({
            mod: value
        })
        if (value === 0) {
            that.state.world.gravity.y = 1;
            Composite.add(that.state.world, that.state.ground);
            clearInterval(that.state.updateInterval);
            clearInterval(that.state.addInterval);
        } else {
            that.state.world.gravity.y = -0.15;
            Composite.remove(that.state.world, that.state.ground);
            const updateInterval = setInterval(function () {
                that.oldStack.forEach((stack) => {
                    stack.bodies.forEach((object) => {
                        if (object.position.y > 0) {
                            if (Math.random() > 0.8) {
                                object.position.x = (Math.random() - 0.5) + object.position.x
                            }
                        }
                    })
                })
            }, 50);
            const addInterval = setInterval(function () {
                let num = 0
                that.oldStack.forEach((stack) => {
                    num += stack.bodies.length
                })
                if (num < 50 && !that.state.showModal) {
                    const stack = Composites.stack(window.innerWidth / 4, window.innerHeight - 1, 6, 5, 0, 0, select);
                    that.oldStack.push(stack)
                    Composite.add(that.state.world, stack);
                }
            }, 1000);
            const removeInterval = setInterval(function () {
                let newStack = [];
                that.oldStack.forEach((stack) => {
                    stack.bodies.forEach((body) => {
                        if (body.position.y < -450) {
                            Composite.remove(that.state.world, body, true);
                        }
                    })
                    if (stack.bodies.length > 0) {
                        newStack.push(stack)
                    }
                })
                that.oldStack = newStack
            }, 1000);
            this.setState({
                updateInterval,
                addInterval,
                removeInterval
            })
        }
    }


    render() {
        return (
            <div>
                <Radio.Group style={{position: "absolute", right: 10, top: 10}} value={this.state.mod}
                             onChange={(e) => {
                                 const value = e.target.value
                                 this.UpdateMod(value)
                             }}>
                    <Radio.Button 
                                  value={0}>重力模式</Radio.Button>
                    <Radio.Button 
                                  value={1}>漂浮模式</Radio.Button>
                </Radio.Group>
                <Modal
                    title={null}
                    centered
                    visible={this.state.showModal}
                    onOk={() => {
                        this.setState({
                            showModal: false
                        });
                        this.oldStack.forEach((stack) => {
                            stack.bodies.forEach((body) => {
                                body.isStatic = false
                            })
                        })
                        if (this.state.mod === 0) {
                            this.UpdateMod(1)
                        }
                    }}
                    onCancel={() => {
                        this.setState({
                            showModal: false
                        });
                        this.oldStack.forEach((stack) => {
                            stack.bodies.forEach((body) => {
                                body.isStatic = false
                            })
                        })
                        if (this.state.mod === 0) {
                            this.UpdateMod(1)
                        }
                    }}
                    width={window.innerWidth / 4 * 3}
                    height={window.innerHeight / 4 * 3}
                    footer={null}
                >
                    <BotUI
                        messages={this.state.messages}
                        action={{
                            type: 'text',
                            action: {
                                placeholder: '输入你的问题',
                            },
                        }}
                        onAction={this.handleAction}
                    />
                </Modal>
            </div>
        )
    }
}
